import React from 'react';
import { Typography, Container, Button } from '@mui/material';
import { Link as RouterLink, useNavigate } from 'react-router-dom';

const HomePage = () => {
    const navigate = useNavigate();
    const logout = () => {
        localStorage.removeItem("PlayFabId");
        localStorage.removeItem("Username");
        sessionStorage.removeItem("SessionID");
        navigate("/");
    }

    return (
        <Container maxWidth="sm">
            <Typography component="h1" variant="h4" align="center">Welcome {localStorage.getItem("Username")}</Typography>
            <Typography component="p" variant="body1">Your PlayFab ID is {localStorage.getItem("PlayFabId")}</Typography>
            <Button onClick={logout} variant="text">Log out</Button>
        </Container>
    )
}

export default HomePage
