import React from 'react';
import { Typography, Container, Stack, Button } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';

const MainPage = () => {
    return (
        <Container maxWidth="sm">
            <Typography component="h1" variant="h4" align="center">Page is under construction</Typography>
            <Typography component="p" variant="body1">While this page is being built, just sign up or sign in to see something</Typography>
            <Stack spacing={2} direction="row" alignItems="center" justifyContent="center">
                <Button component={RouterLink} to='/signup' variant="outlined">Sign up</Button>
                <Button component={RouterLink} to='/signin' variant="outlined">Sign in</Button>
            </Stack>
        </Container>
    )
}

export default MainPage
