import React from 'react'
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { TextField, Button, Box, Container, Typography } from '@mui/material';
import { Link as RouterLink, useNavigate } from 'react-router-dom';
import axios from 'axios';

const schema = Yup.object().shape({
    username: Yup.string().required().min(4).max(20),
    password: Yup.string().required().min(8).max(20),
});

const SignIn = () => {
    let navigate = useNavigate();

    const {
        register,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
        defaultValues: {
            username: "",
            password: "",
        }
    });

    const onSubmit = (data) => {
        let modifiedData = {
            Username: data.username,
            Password: data.password,
            TitleID: process.env.REACT_APP_PLAYFAB_TITLE_ID
        };
        console.log(modifiedData);
        axios({
            method: 'post',
            url: `https://${process.env.REACT_APP_PLAYFAB_TITLE_ID
                }.playfabapi.com/Client/LoginWithPlayFab`,
            data: modifiedData
        })
            .then((res) => {
                console.log(res.data);
                localStorage.setItem("PlayFabId", res.data.data.PlayFabId);
                localStorage.setItem("Username", modifiedData.Username);
                sessionStorage.setItem("SessionID", res.data.data.SessionTicket);
                navigate("/home");
            })
            .catch((error) => {
                if (error.response) {
                    // The request was made and the server responded with a status code
                    // that falls out of the range of 2xx
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                } else if (error.request) {
                    // The request was made but no response was received
                    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                    // http.ClientRequest in node.js
                    console.log(error.request);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }

    return (
        <Container maxWidth="xs">
            <Box component="div">
                <Typography variant="h3" component="h1" align="center">
                    Sign In
                </Typography>
            </Box>
            <form onSubmit={handleSubmit(onSubmit)}>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="username" render={({
                        field: { onChange, onBlur, value, name, ref },
                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.username ? true : false}
                            helperText={errors.username?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Username"
                            variant="outlined"
                        />)}
                    />
                </Box>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="password" render={({
                        field: { onChange, onBlur, value, name, ref },
                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.password ? true : false}
                            helperText={errors.password?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Password"
                            variant="outlined"
                            type="password"
                        />)}
                    />
                </Box>
                <Button type="submit" fullWidth variant="contained" color="primary">
                    Sign In
                </Button>
            </form>
            <Box component="div" sx={{ py: 2 }}>
                <Typography variant="p" component="p">Don't have an account? <Button component={RouterLink} to="/signup">Sign Up</Button></Typography>
            </Box>
        </Container>
    )
}

export default SignIn