import React from 'react'
import { useForm, Controller } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { TextField, Button, Box, Container, Typography, FormControlLabel, FormControl, FormHelperText, Checkbox } from '@mui/material';
import { Link as RouterLink } from 'react-router-dom';
import axios from 'axios';

const schema = Yup.object().shape({
    username: Yup.string().required().min(4).max(20),
    email: Yup.string().email().max(50).required(),
    password: Yup.string().required().min(8).max(20),
    confirmPassword: Yup.string()
        .required()
        .oneOf([Yup.ref("password"), null], "Password and Confirm Password should be the same"),
    acceptTerm: Yup.bool().oneOf([true], "Accept Terms is required"),
});

const SignUp = () => {
    const {
        register,
        handleSubmit,
        formState: { errors },
        control,
    } = useForm({
        resolver: yupResolver(schema),
        defaultValues: {
            username: "",
            email: "",
            password: "",
            confirmPassword: "",
            acceptTerm: false,
        }
    });

    const onSubmit = (data) => {
        let modifiedData = {
            Username: data.username,
            Email: data.email,
            Password: data.password,
            TitleID: process.env.REACT_APP_PLAYFAB_TITLE_ID
        };
        console.log(modifiedData);
        axios({
            method: 'post',
            url: `https://${process.env.REACT_APP_PLAYFAB_TITLE_ID
                }.playfabapi.com/Client/RegisterPlayFabUser`,
            data: modifiedData
        })
            .then((res) => {
                console.log(res.data);
            })
            .catch((error) => {
                if (error.response) {
                    // The request was made and the server responded with a status code
                    // that falls out of the range of 2xx
                    console.log(error.response.data);
                    console.log(error.response.status);
                    console.log(error.response.headers);
                } else if (error.request) {
                    // The request was made but no response was received
                    // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
                    // http.ClientRequest in node.js
                    console.log(error.request);
                } else {
                    // Something happened in setting up the request that triggered an Error
                    console.log('Error', error.message);
                }
                console.log(error.config);
            });
    }

    return (
        <Container maxWidth="xs">
            <Box component="div">
                <Typography variant="h3" component="h1" align="center">
                    Sign Up
                </Typography>
            </Box>
            <form onSubmit={handleSubmit(onSubmit)}>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="username" render={({
                        field: { onChange, onBlur, value, name, ref },
                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.username ? true : false}
                            helperText={errors.username?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Username"
                            variant="outlined"
                        />)}
                    />
                </Box>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="email" render={({
                        field: { onChange, onBlur, value, name, ref },
                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.email ? true : false}
                            helperText={errors.email?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Email"
                            variant="outlined"
                            type="email"
                        />)}
                    />
                </Box>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="password" render={({
                        field: { onChange, onBlur, value, name, ref },
                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.password ? true : false}
                            helperText={errors.password?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Password"
                            variant="outlined"
                            type="password"
                        />)}
                    />
                </Box>
                <Box component="div" sx={{ py: 2 }}>
                    <Controller control={control} name="confirmPassword" render={({
                        field: { onChange, onBlur, value, name, ref },

                    }) => (
                        <TextField
                            required
                            fullWidth
                            error={errors.confirmPassword ? true : false}
                            helperText={errors.confirmPassword?.message}
                            value={value}
                            onChange={onChange} // send value to hook form
                            onBlur={onBlur} // notify when input is touched
                            inputRef={ref} // wire up the input ref
                            label="Confirm Password"
                            variant="outlined"
                            type="password"
                        />)}
                    />
                </Box>
                <Box component="div" sx={{ py: 2 }}>
                    <FormControl error={errors.acceptTerm ? true : false}>
                        <FormControlLabel
                            control={
                                <Controller
                                    name="acceptTerm"
                                    control={control}
                                    // defaultValue={false}
                                    render={({
                                        field: { onChange, onBlur, value, name, ref },

                                    }) => (<Checkbox
                                        value={value}
                                        onChange={onChange} // send value to hook form
                                        inputRef={ref} // wire up the input ref
                                        color="primary" />
                                    )}
                                />
                            }
                            label={"Agree to Term of services"}
                        />
                        {errors.acceptTerm && <FormHelperText>{errors.acceptTerm?.message}</FormHelperText>}
                    </FormControl>
                </Box>

                <Button type="submit" fullWidth variant="contained" color="primary">
                    Register
                </Button>
            </form>
            <Box component="div" sx={{ py: 2 }}>
                <Typography variant="p" component="p">Have an account? <Button component={RouterLink} to="/signin">Sign in</Button></Typography>
            </Box>
        </Container>
    )
}

export default SignUp